<template>
  <div class="at-page">
    <h2 class="at-title">Îã¥ÎãπÏûê Í∂åÌïú(Ïù¥Ïö©Ïûê) Ïù¥Ï†Ñ</h2>

    <!-- Í∂åÌïú ÏóÜÏùå -->
    <div v-if="!isOrgManager" class="at-no-auth">
      <p>Ï†ëÍ∑º Í∂åÌïúÏù¥ ÏóÜÏäµÎãàÎã§. (Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê Ï†ÑÏö© ÌéòÏù¥ÏßÄ)</p>
    </div>

    <!-- Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê Ï†ÑÏö© -->
    <div v-else>
      <!-- ÏÉÅÎã® ÌïÑÌÑ∞/ÏÑ†ÌÉù ÏòÅÏó≠ -->
      <div class="at-toolbar">
        <!-- 1Ìñâ: Îã¥ÎãπÏûê ÏÑ†ÌÉù -->
        <div class="at-row">
          <div class="at-field-group">
            <label class="at-label">ÌòÑÏû¨ Îã¥ÎãπÏûê(From)</label>
            <select
              v-model="sourceManagerCode"
              class="at-select"
              @change="onSourceManagerChange"
            >
              <option value="">Îã¥ÎãπÏûê ÏÑ†ÌÉù</option>
              <option
                v-for="m in managerOptions"
                :key="m.user_code"
                :value="m.user_code"
              >
                {{ m.name }}
              </option>
            </select>
          </div>

          <div class="at-field-group">
            <label class="at-label">Ïù¥Ï†ÑÎ∞õÏùÑ Îã¥ÎãπÏûê(To)</label>
            <select
              v-model="targetManagerCode"
              class="at-select"
              @change="onTargetManagerChange"
            >
              <option value="">Îã¥ÎãπÏûê ÏÑ†ÌÉù</option>
              <option
                v-for="m in managerOptions"
                :key="m.user_code"
                :value="m.user_code"
              >
                {{ m.name }}
              </option>
            </select>
          </div>

          <div class="at-field-group at-field-grow">
            <label class="at-label">Ïù¥Ïö©Ïûê Í≤ÄÏÉâ(Ïù¥Î¶Ñ/ID Îì±)</label>
            <div class="at-inline">
              <input
                v-model.trim="keyword"
                class="at-input"
                placeholder="Ïù¥Ïö©Ïûê Ïù¥Î¶Ñ ÎòêÎäî ID Í≤ÄÏÉâ"
                @keyup.enter="fetchSourceUsers"
              />
              <button class="at-btn" @click="fetchSourceUsers">Í≤ÄÏÉâ</button>
            </div>
          </div>
        </div>

        <!-- 2Ìñâ: ÏïàÎÇ¥ Î¨∏Íµ¨ -->
        <div class="at-row at-row-hint">
          <p>
            ‚Ä¢ ÏôºÏ™ΩÏóêÏÑú <strong>Ïù¥Ï†ÑÌï† Ïù¥Ïö©Ïûê</strong>Î•º ÏÑ†ÌÉùÌïòÍ≥†, ÏïÑÎûòÏùò
            <strong>Í∂åÌïú Ïù¥Ï†Ñ</strong> Î≤ÑÌäºÏùÑ ÎàÑÎ•¥Î©¥ ÏÑ†ÌÉùÌïú Ïù¥Ïö©ÏûêÏùò Îã¥ÎãπÏûêÍ∞Ä
            <strong>{{ targetManagerName || "ÏÑ†ÌÉùÎêú Îã¥ÎãπÏûê" }}</strong
            >(Ïúº)Î°ú Î≥ÄÍ≤ΩÎê©ÎãàÎã§.
          </p>
        </div>
      </div>

      <!-- Î≥∏Î¨∏: Îëê Í∞úÏùò Ìå®ÎÑê -->
      <div class="at-panels">
        <!-- ÏôºÏ™Ω: ÌòÑÏû¨ Îã¥ÎãπÏûê Ïù¥Ïö©Ïûê Î™©Î°ù -->
        <div class="at-panel">
          <div class="at-panel-header">
            <h3 class="at-panel-title">
              ÌòÑÏû¨ Îã¥ÎãπÏûê Ïù¥Ïö©Ïûê Î™©Î°ù
              <span v-if="sourceManagerName" class="at-tag">
                {{ sourceManagerName }}
              </span>
            </h3>
            <small class="at-panel-sub">
              Ï†ÑÏ≤¥ {{ sourceUsers.length }}Î™Ö
            </small>
          </div>

          <div class="at-table-wrap">
            <table class="at-table">
              <thead>
                <tr>
                  <th>
                    <input
                      type="checkbox"
                      :checked="isAllSelected"
                      @change="toggleSelectAll"
                    />
                  </th>
                  <th>No</th>
                  <th>Ïù¥Ïö©ÏûêÎ™Ö</th>
                  <th>ÏãùÎ≥ÑÏûê(ID Îì±)</th>
                </tr>
              </thead>
              <tbody>
                <tr v-if="loadingSource">
                  <td colspan="4" class="at-empty">Î°úÎî© Ï§ë...</td>
                </tr>
                <tr v-else-if="sourceUsers.length === 0">
                  <td colspan="4" class="at-empty">
                    Ï°∞ÌöåÎêú Ïù¥Ïö©ÏûêÍ∞Ä ÏóÜÏäµÎãàÎã§.
                  </td>
                </tr>
                <tr v-for="(user, idx) in sourceUsers" :key="user.user_code">
                  <td>
                    <input
                      type="checkbox"
                      v-model="selectedUserCodes"
                      :value="user.user_code"
                    />
                  </td>
                  <td>{{ idx + 1 }}</td>
                  <td>{{ getUserName(user) }}</td>
                  <td>{{ getUserIdentifier(user) }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>

        <!-- Ïò§Î•∏Ï™Ω: ÎåÄÏÉÅ Îã¥ÎãπÏûê Ïù¥Ïö©Ïûê Î™©Î°ù -->
        <div class="at-panel">
          <div class="at-panel-header">
            <h3 class="at-panel-title">
              Ïù¥Ï†ÑÎ∞õÏùÑ Îã¥ÎãπÏûê Ïù¥Ïö©Ïûê Î™©Î°ù
              <span v-if="targetManagerName" class="at-tag at-tag-secondary">
                {{ targetManagerName }}
              </span>
            </h3>
            <small class="at-panel-sub">
              Ï†ÑÏ≤¥ {{ targetUsers.length }}Î™Ö
            </small>
          </div>

          <div class="at-table-wrap">
            <table class="at-table">
              <thead>
                <tr>
                  <th>No</th>
                  <th>Ïù¥Ïö©ÏûêÎ™Ö</th>
                  <th>ÏãùÎ≥ÑÏûê(ID Îì±)</th>
                </tr>
              </thead>
              <tbody>
                <tr v-if="loadingTarget">
                  <td colspan="3" class="at-empty">Î°úÎî© Ï§ë...</td>
                </tr>
                <tr v-else-if="targetUsers.length === 0">
                  <td colspan="3" class="at-empty">
                    Ï°∞ÌöåÎêú Ïù¥Ïö©ÏûêÍ∞Ä ÏóÜÏäµÎãàÎã§.
                  </td>
                </tr>
                <tr v-for="(user, idx) in targetUsers" :key="user.user_code">
                  <td>{{ idx + 1 }}</td>
                  <td>{{ getUserName(user) }}</td>
                  <td>{{ getUserIdentifier(user) }}</td>
                </tr>
              </tbody>
            </table>
          </div>
        </div>
      </div>

      <!-- ÌïòÎã®: Ïï°ÏÖò Î≤ÑÌäº -->
      <div class="at-actions">
        <button
          class="at-btn at-btn-primary"
          :disabled="!canTransfer || transferring"
          @click="openConfirmModal"
        >
          {{ transferring ? "Ïù¥Ï†Ñ Ï≤òÎ¶¨ Ï§ë..." : "ÏÑ†ÌÉùÌïú Ïù¥Ïö©Ïûê Í∂åÌïú Ïù¥Ï†Ñ" }}
        </button>
      </div>
    </div>

    <!-- üî∂ Í∂åÌïú Ïù¥Ï†Ñ ÌôïÏù∏ Î™®Îã¨ -->
    <Teleport to="body">
      <div v-if="showConfirm" class="at-modal-backdrop">
        <div class="at-modal">
          <h3 class="at-modal-title">Í∂åÌïú Ïù¥Ï†Ñ ÌôïÏù∏</h3>

          <p class="at-modal-text">
            ÏÑ†ÌÉùÌïú {{ selectedUserCodes.length }}Î™ÖÏùò Ïù¥Ïö©ÏûêÎ•º<br />
            <strong>{{ sourceManagerName }}</strong> ‚Üí
            <strong>{{ targetManagerName }}</strong
            ><br />
            Î°ú Ïù¥Ï†ÑÌïòÏãúÍ≤†ÏäµÎãàÍπå?
          </p>

          <div class="at-modal-actions">
            <button class="at-btn" @click="cancelTransfer">Ï∑®ÏÜå</button>
            <button class="at-btn at-btn-primary" @click="confirmTransfer">
              Ïòà, Ïù¥Ï†ÑÌïòÍ∏∞
            </button>
          </div>
        </div>
      </div>
    </Teleport>
  </div>
</template>

<script setup>
import { computed, onMounted, ref } from "vue";
import axios from "axios";
import { useAuthStore } from "@/store/authLogin";

const auth = useAuthStore();

const sourcePage = ref(1);
const sourceTotalCount = ref(0);
const targetPage = ref(1);
const targetTotalCount = ref(0);

// Í∂åÌïú Ï≤¥ÌÅ¨: Í∏∞Í¥Ä Í¥ÄÎ¶¨ÏûêÎßå
const isOrgManager = computed(() => auth.role === "AA3");

// Îã¥ÎãπÏûê Î™©Î°ù (Í∞ôÏùÄ Í∏∞Í¥Ä AA2)
const managerOptions = ref([]);

// ÏÑ†ÌÉùÎêú Îã¥ÎãπÏûê
const sourceManagerCode = ref("");
const targetManagerCode = ref("");

// Ïù¥Ïö©Ïûê Í≤ÄÏÉâ ÌÇ§ÏõåÎìú (ÌòÑÏû¨ Îã¥ÎãπÏûê Ï™ΩÎßå)
const keyword = ref("");

// Ïù¥Ïö©Ïûê Î™©Î°ù
const sourceUsers = ref([]);
const targetUsers = ref([]);

// ÏÑ†ÌÉùÎêú Ïù¥Ïö©Ïûê (user_code Î∞∞Ïó¥)
const selectedUserCodes = ref([]);

// Î°úÎî© ÌîåÎûòÍ∑∏
const loadingSource = ref(false);
const loadingTarget = ref(false);
const transferring = ref(false);

// üî∂ Î™®Îã¨ ON/OFF
const showConfirm = ref(false);

// Îã¥ÎãπÏûê Ïù¥Î¶Ñ computed
const sourceManagerName = computed(() => {
  const m = managerOptions.value.find(
    (x) => String(x.user_code) === String(sourceManagerCode.value)
  );
  return m ? m.name : "";
});

const targetManagerName = computed(() => {
  const m = managerOptions.value.find(
    (x) => String(x.user_code) === String(targetManagerCode.value)
  );
  return m ? m.name : "";
});

// Ï†ÑÏ≤¥ ÏÑ†ÌÉù Ï≤¥ÌÅ¨Î∞ïÏä§ ÏÉÅÌÉú
const isAllSelected = computed(() => {
  return (
    sourceUsers.value.length > 0 &&
    selectedUserCodes.value.length === sourceUsers.value.length
  );
});

// Í∂åÌïú Ïù¥Ï†Ñ Í∞ÄÎä• Ïó¨Î∂Ä
const canTransfer = computed(() => {
  return (
    sourceManagerCode.value &&
    targetManagerCode.value &&
    sourceManagerCode.value !== targetManagerCode.value &&
    selectedUserCodes.value.length > 0
  );
});

// Í≥µÌÜµ: Ïù¥Ïö©Ïûê Ïù¥Î¶Ñ/ÏãùÎ≥ÑÏûê ÌëúÏãú Ìó¨Ìçº
const getUserName = (user) =>
  user.user_name || user.child_name || user.name || "-";

const getUserIdentifier = (user) =>
  user.user_id || user.child_code || user.identifier || "";

// Îã¥ÎãπÏûê Î™©Î°ù Ï°∞Ìöå (Í∏∞Í¥Ä Í¥ÄÎ¶¨Ïûê Î°úÍ∑∏Ïù∏ Í∏∞Ï§Ä)
const fetchManagerOptions = async () => {
  if (!isOrgManager.value) return;
  try {
    const res = await axios.get("/api/managers/simple", {
      params: { loginId: auth.userId }, // Î°úÍ∑∏Ïù∏ ÏïÑÏù¥Îîî Í∏∞Ï§Ä
    });

    const raw = res.data?.data ?? [];
    managerOptions.value = raw.map((m) => ({
      user_code: m.user_code ?? m.manager_code,
      name: m.name ?? m.manager_name,
    }));
  } catch (err) {
    console.error("[AuthorityTransfer] Îã¥ÎãπÏûê Î™©Î°ù Ï°∞Ìöå Ïã§Ìå®:", err);
  }
};

// ÌòÑÏû¨ Îã¥ÎãπÏûê Ïù¥Ïö©Ïûê Î™©Î°ù
const fetchSourceUsers = async () => {
  if (!sourceManagerCode.value) {
    sourceUsers.value = [];
    selectedUserCodes.value = [];
    return;
  }

  loadingSource.value = true;
  try {
    const res = await axios.get("/api/authority-transfer/users", {
      params: {
        loginId: auth.userId,
        managerCode: sourceManagerCode.value,
        keyword: keyword.value || "",
        page: sourcePage.value,
        size: 20,
      },
    });

    const data = res.data?.data ?? {};
    sourceUsers.value = data.list ?? data; // Îëò Ï§ë Î≠êÍ∞Ä ÏôÄÎèÑ ÎåÄÏùë
    sourceTotalCount.value = data.totalCount ?? sourceUsers.value.length;
  } catch (err) {
    console.error("[AuthorityTransfer] sourceUsers Ï°∞Ìöå Ïã§Ìå®:", err);
  } finally {
    loadingSource.value = false;
  }
};

// ÎåÄÏÉÅ Îã¥ÎãπÏûê Ïù¥Ïö©Ïûê Î™©Î°ù
const fetchTargetUsers = async () => {
  if (!targetManagerCode.value) {
    targetUsers.value = [];
    return;
  }

  loadingTarget.value = true;
  try {
    const res = await axios.get("/api/authority-transfer/users", {
      params: {
        loginId: auth.userId,
        managerCode: targetManagerCode.value,
        page: targetPage.value,
        size: 20,
      },
    });

    const data = res.data?.data ?? {};
    targetUsers.value = data.list ?? data;
    targetTotalCount.value = data.totalCount ?? targetUsers.value.length;
  } catch (err) {
    console.error("[AuthorityTransfer] targetUsers Ï°∞Ìöå Ïã§Ìå®:", err);
  } finally {
    loadingTarget.value = false;
  }
};

// Ï†ÑÏ≤¥ ÏÑ†ÌÉù ÌÜ†Í∏Ä
const toggleSelectAll = () => {
  if (isAllSelected.value) {
    selectedUserCodes.value = [];
  } else {
    selectedUserCodes.value = sourceUsers.value.map((u) => u.user_code);
  }
};

// Îã¥ÎãπÏûê Î≥ÄÍ≤Ω Ïãú Î°úÎî©
const onSourceManagerChange = () => {
  keyword.value = "";
  fetchSourceUsers();
};

const onTargetManagerChange = () => {
  fetchTargetUsers();
};

// üî∂ Î™®Îã¨ Ïó¥Í∏∞
const openConfirmModal = () => {
  if (!canTransfer.value) return;
  showConfirm.value = true;
};

// üî∂ Î™®Îã¨ Îã´Í∏∞
const cancelTransfer = () => {
  showConfirm.value = false;
};

// Í∂åÌïú Ïù¥Ï†Ñ Ïã§Ìñâ (Ïã§Ï†ú Ï≤òÎ¶¨)
const onTransfer = async () => {
  if (!canTransfer.value) return;

  transferring.value = true;
  try {
    await axios.post("/api/authority-transfer/transfer", {
      loginId: auth.userId,
      fromManagerCode: sourceManagerCode.value,
      toManagerCode: targetManagerCode.value,
      userCodes: selectedUserCodes.value,
    });

    alert("Í∂åÌïú(Îã¥ÎãπÏûê) Ïù¥Ï†ÑÏù¥ ÏôÑÎ£åÎêòÏóàÏäµÎãàÎã§.");

    // Î™©Î°ù ÏÉàÎ°úÍ≥†Ïπ®
    await fetchSourceUsers();
    await fetchTargetUsers();
    selectedUserCodes.value = [];
  } catch (err) {
    console.error("[AuthorityTransfer] Í∂åÌïú Ïù¥Ï†Ñ Ïã§Ìå®:", err);
    alert("Í∂åÌïú Ïù¥Ï†Ñ Ï≤òÎ¶¨ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
  } finally {
    transferring.value = false;
  }
};

// üî∂ Î™®Îã¨ÏóêÏÑú ‚ÄúÏòà, Ïù¥Ï†ÑÌïòÍ∏∞‚Äù Î≤ÑÌäº ÌÅ¥Î¶≠
const confirmTransfer = async () => {
  showConfirm.value = false;
  await onTransfer();
};

onMounted(async () => {
  if (!isOrgManager.value) return;
  await fetchManagerOptions();
});
</script>

<style scoped>
