  padding: 30px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background: #fafafa;
  text-align: center;
  font-size: 14px;
}

.at-toolbar {
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.at-row {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: flex-end;
}

.at-row-hint {
  font-size: 12px;
  color: #666;
}

.at-field-group {
  display: flex;
  flex-direction: column;
  gap: 4px;
