}

.at-panels {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
}

.at-panel {
  flex: 1;
  min-width: 320px;
  border: 1px solid #eee;
  border-radius: 8px;
  background: #fff;
}

.at-panel-header {
  padding: 10px 12px;
  border-bottom: 1px solid #f0f0f0;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  gap: 8px;
}

.at-panel-title {
  font-size: 14px;
  font-weight: 600;
  display: flex;
  align-items: center;
