}

.at-actions {
  margin-top: 16px;
  display: flex;
  justify-content: center;
}

/* üî∂ Î™®Îã¨ Î∞±Í∑∏ÎùºÏö¥Îìú */
.at-modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

/* üî∂ Î™®Îã¨ Î∞ïÏä§ */
.at-modal {
  width: 320px;
  background: #fff;
  border-radius: 10px;
  padding: 20px 22px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
}

.at-modal-title {
  font-size: 16px;
